@font-face {
  font-family: "MuseoCyrl";
  src: local("MuseoCyrl-Bold"), url("../fonts/MuseoCyrl700.woff") format("woff");
  font-weight: 700;
  font-style: bold;
  font-display: swap; }

@font-face {
  font-family: "MuseoCyrl";
  src: local("MuseoCyrl-Medium"), url("../fonts/MuseoCyrl500.woff") format("woff");
  font-weight: 500;
  font-style: regular;
  font-display: swap; }

@font-face {
  font-family: "MuseoCyrl";
  src: local("MuseoCyrl-Regular"), url("../fonts/MuseoCyrl300.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  margin: 0 auto;
  padding: 0;
  font-family: "MuseoCyrl";
  background-color: #ffffff;
  min-width: 320px;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden; }
  body.no-scroll {
    overflow: hidden; }

main {
  position: relative;
  z-index: 1; }

a {
  text-decoration: none;
  cursor: pointer; }

h1 {
  padding: 0;
  margin: 0; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

.visually-hidden {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.container {
  width: 1920px;
  margin: 0 auto;
  padding: 0 80px;
  width: 1920px;
  margin: 0 auto;
  padding: 0 369px; }
  @media (max-width: 1920px) {
    .container {
      width: 768px;
      padding: 0 20px; } }
  @media (max-width: 768px) {
    .container {
      width: 320px;
      padding: 0 20px; } }
  @media (max-width: 320px) {
    .container {
      width: 100%;
      padding: 0 20px; } }
  @media (max-width: 768px) {
    .container {
      width: 768px;
      padding: 0 45px; } }
  @media (max-width: 320px) {
    .container {
      width: 320px;
      padding: 0 10px; } }

@keyframes bounce {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(-20px); }
  100% {
    transform: translateY(0); } }

@keyframes flash {
  0% {
    transform: scale(0.8); }
  50% {
    transform: scale(1.2); }
  100% {
    transform: scale(0.8); } }

@media (max-width: 320px) {
  @keyframes bounce {
    0% {
      transform: translateY(20px); }
    50% {
      transform: translateY(-10px); }
    100% {
      transform: translateY(20px); } } }

.header__navigation {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  align-items: center; }

.navigation__logo {
  margin-right: 32px; }

.navigation__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 802px;
  margin-right: 76px; }

.navigation__link {
  display: flex;
  flex-direction: row; }

.navigation__item a {
  font-size: 18px;
  line-height: 24px; }

.navigation__whatsapp {
  margin-right: 10px; }
